<template>
  <div class="hello">
    <el-row>
    	<el-col :span='7' class="pos-order" id="order-list">
		    <el-tabs>
			    <el-tab-pane label="点餐">
			    	<el-table :data="tableData" style="width: 100%" border>
				        <el-table-column prop="goodsName" label="品名" ></el-table-column>
				        <el-table-column prop="count" label="数量" ></el-table-column>
				        <el-table-column prop="price" label="价格" ></el-table-column>
				        <el-table-column label="操作" fixed="right" width="150">
				        	<template scope="scope">
				        		<el-button type="success" size="small" @click="addOrderList(scope.row)">增加</el-button>
				        		<el-button type="danger" size="small" @click="deleteGood(scope.row)">删除</el-button>
				        	</template>
				        </el-table-column>
				    </el-table>
				    <div class="total">
				    	<div>总数：{{totalCount}}</div>
				    	<div>总价：{{totalMoney}}元</div>
				    </div>
				    <div class="btn">
				    	<el-button type="warning" size="small">挂单</el-button>
		        		<el-button type="danger" size="small" @click="deleteAll">删除</el-button>
		        		<el-button type="success" size="small" @click="checkOut">结账</el-button>
				    </div>

			    </el-tab-pane>
			    <el-tab-pane label="挂单">挂单</el-tab-pane>
  			</el-tabs>
    	</el-col>
    	<el-col :span='17'>
    		<div class="list">
	    		<div id="title">商品</div>
	    		<div class="goodsList">
	    			<ul>
	    				<li v-for="item in goodsList" @click="addOrderList(item)">
	    					<span>{{item.goodsName}}</span>
	    					<span>￥{{item.price}}元</span>
	    				</li>
	    			</ul>
	    		</div>
    		</div>
			<div class="goodsType">
	    		<el-tabs>
				    <el-tab-pane label="汉堡">
				    	<div>
				    		<ul class="cookList">
				    			<li v-for="item in type0Goods" @click="addOrderList(item)">
				    				<span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
							        <span class="foodName">{{item.goodsName}}</span>
							        <span class="foodPrice">￥{{item.price}}元</span>
				    			</li>
				    		</ul>
				    	</div>
				    </el-tab-pane>
				    <el-tab-pane label="饮料">
				    	<div>
				    		<ul class="cookList">
				    			<li v-for="item in type1Goods" @click="addOrderList(item)">
				    				<span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
							        <span class="foodName">{{item.goodsName}}</span>
							        <span class="foodPrice">￥{{item.price}}元</span>
				    			</li>
				    		</ul>
				    	</div>
				    </el-tab-pane>
				    <el-tab-pane label="套餐">
				    	<div>
				    		<ul class="cookList">
				    			<li v-for="item in type2Goods" @click="addOrderList(item)">
				    				<span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
							        <span class="foodName">{{item.goodsName}}</span>
							        <span class="foodPrice">￥{{item.price}}元</span>
				    			</li>
				    		</ul>
				    	</div>
				    </el-tab-pane>
  				</el-tabs>
    		</div>

    	</el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Pos',
  data(){
  	return{
  		tableData:[],
  		goodsList:[],
  		type0Goods:[],
  		type1Goods:[],
  		type2Goods:[],
  		totalMoney:0,
  		totalCount:0
  	}
  },
  created:function(){
  	axios.get('http://jspang.com/DemoApi/oftenGoods.php')
  	.then(reponse=>{
  		//console.log(reponse.data)
  		this.goodsList=reponse.data
  	})
  	.catch(error=>{
  		alert('网络错误')
  	})

  	axios.get('http://jspang.com/DemoApi/typeGoods.php')
  	.then(reponse=>{
  		//console.log(reponse.data)
  		this.type0Goods=reponse.data[0]
  		this.type1Goods=reponse.data[2]
  		this.type2Goods=reponse.data[3]
  	})
  	.catch(error=>{
  		alert('网络错误')
  	})
  },
  mounted:function(){
  	var height=document.body.clientHeight;
  	document.getElementById("order-list").style.height=height+'px';
  },
  methods:{
  	addOrderList(goods){
  		//是否存在该商品
  		this.totalMoney=0;
  		this.totalCount=0;
  		let isHave=false;
  		for(let i=0;i<this.tableData.length;i++){
  			if(this.tableData.goodsId===goods.goodsId){
  				isHave=true;
  			}
  		}
  		//判断后
  		if(isHave){
  			//改变数量
  			let arr=this.tableData.filter(a=>a.goodsId==goods.goodsId);
  			console.log('have')
  			arr[1].count++
  		}else{
  			let newGoods={
  				goodsId:goods.goodsId,
  				goodsName:goods.goodsName,
  				price:goods.price,
  				count:1
  			}
  			this.tableData.push(newGoods)
  		}
  		this.getTotal()
  	},
  	//删除单品
  	deleteGood(goods){
  		this.tableData=this.tableData.filter(a=>a.goodsId!=goods.goodsId);
  		this.getTotal()
  	},
  	deleteAll(){
  		this.tableData=[];
  		this.totalMoney=0;
  		this.totalCount=0
  	},
  	//结账
  	checkOut(){
  		if(this.totalCount!=0){
  			this.tableData=[];
	  		this.totalMoney=0;
	  		this.totalCount=0;
	  		this.$message({
	  			message:'支付成功!',
	  			type:'success'
	  		})
  		}else{
  			this.$message.error('请点餐')
  		}
  	},
  	//总量
  	getTotal(){
  		this.totalCount=0;
  		this.totalMoney=0;
  		if(this.tableData){
  			this.tableData.forEach((element)=>{
	  			this.totalCount+=element.count;
	  			this.totalMoney=this.totalMoney+(element.price*element.count)
  			})
  		}
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos-order{
	background:#f9fafc;
	border-right:1px solid #c0ccda;
}
.btn{
	margin-top:15px;
}
li{
	list-style:none;
	cursor:pointer;
}
//商品名
.list #title{
	height:24px;
	background:#f9fafc;
	padding-top:10px;
	line-height:24px;
	background:red;
	border-bottom:1px solid #f9fafc;
}
.goodsList ul li{
	list-style:none;
	border:1px solid #f9fafc;
	padding:10px;
	float:left;
	border-radius:3px;
	margin:10px;
	background:#fff;
	cursor:pointer;
}
.goodsList ul:after{
	content:'';
	height:0;
	display:block;
	clear:both;
	visibility:hidden;
}

.cookList li{
   list-style: none;
   width:23%;
   border:1px solid #E5E9F2;
   height: auot;
   overflow: hidden;
   background-color:#fff;
   padding: 2px;
   float:left;
   margin: 2px;
 
}
.cookList li span{
   
    display: block;
    float:left;
}
.foodImg{
   width: 40%;
}
.foodName{
   font-size: 18px;
   padding-left: 10px;
   color:brown;

}
.foodPrice{
   font-size: 16px;
   padding-left: 10px;
   padding-top:10px;
}

.total{
	display:flex;
	border-bottom:1px solid #E5E9F2;
}
.total div{
text-align:left;
padding-left:15px;
	line-height:36px;
	flex:1;
	border-right:1px solid #E5E9F2;
}

</style>



